import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getsupplierList } from "../../actions/supplierAction";
import { getToday } from "../../GlobalComponent/function";
import {
  ReanderField,
  ReanderSelect,
} from "../../GlobalComponent/notification";

class formSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kode_perhiasan: "",
      tipeOmzet: "PERTAHUN",
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  render() {
    switch (this.props.from) {
      case "penerimaan":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_supplier"
                  component={ReanderSelect}
                  options={this.props.listsupplier.map((list) => {
                    let data = {
                      value: list.kode_supplier,
                      name: list.nama_supplier,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Supplier"
                  placeholder="Masukan Kode Supplier"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="type"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "DETAIL",
                      name: "DETAIL",
                    },
                    {
                      value: "REKAP",
                      name: "REKAP",
                    },
                  ]}
                  type="text"
                  label="Type Laporan"
                  placeholder="Masukan Type Laporan"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_awal"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_akhir"
                  label="Sampai Dengan"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.onSend}
                  >
                    {this.props.onSend ? (
                      <span
                        className="spinner-border spinner-border-sm mr-3"
                        aria-hidden="true"
                      ></span>
                    ) : (
                      <i className="fa fa-print mr-2"></i>
                    )}
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );

      case "hutang":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_supplier"
                  component={ReanderSelect}
                  options={this.props.listsupplier.map((hasil) => {
                    let data = {
                      value: hasil.kode_supplier,
                      name: hasil.nama_supplier,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Supplier"
                  placeholder="Masukan Kode Supplier"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="type"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "SIMPLE",
                      name: "SIMPLE",
                    },
                    {
                      value: "CARD",
                      name: "CARD",
                    },
                    {
                      value: "SUMMARY",
                      name: "SUMMARY",
                    },
                  ]}
                  type="text"
                  label="Type Laporan"
                  placeholder="Masukan Type Laporan"
                  onChange={(data) =>
                    this.setState({
                      type_laporan: data,
                    })
                  }
                />
              </div>
              <div
                className={
                  this.state.type_laporan === "SIMPLE"
                    ? "col-lg-3 d-none"
                    : "col-lg-3"
                }
              >
                <Field
                  name="tanggal_awal"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Awal"
                  placeholder="Masukan Tanggal Awal"
                />
              </div>
              <div
                className={
                  this.state.type_laporan === "SIMPLE"
                    ? "col-lg-3 d-none"
                    : "col-lg-3"
                }
              >
                <Field
                  name="tanggal_akhir"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Akhir"
                  placeholder="Masukan Tanggal Akhir"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.onSend}
                  >
                    {this.props.onSend ? (
                      <span
                        className="spinner-border spinner-border-sm mr-3"
                        aria-hidden="true"
                      ></span>
                    ) : (
                      <i className="fa fa-print mr-2"></i>
                    )}
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );

      case "omzet":
        return (
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Tipe</label>
              <Field
                name="tipe"
                className="form-control"
                component="select"
                onChange={this.inputChange("tipeOmzet")}
              >
                <option value="PERTAHUN" key="PERTAHUN">
                  PERHATUN
                </option>
                <option value="PERBULAN" key="PERBULAN">
                  PERBULAN
                </option>
                <option value="PERTANGGAL" key="PERTANGGAL">
                  PERTANGGAL
                </option>
              </Field>
            </div>
            {this.state.tipeOmzet === "PERTAHUN" ? (
              <>
                <div className="col-lg-4">
                  <Field
                    name="tahunAwal"
                    label="Tahun Awal"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="tahunAkhir"
                    label="Sampai Dengan"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
              </>
            ) : this.state.tipeOmzet === "PERBULAN" ? (
              <>
                <div className="col-lg-4">
                  <Field
                    name="bulanAwal"
                    label="Bulan Awal"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="bulanAkhir"
                    label="Sampai Dengan"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
              </>
            ) : (
              <>
                <div className="col-lg-4">
                  <Field
                    name="dateAwal"
                    label="Tanggal Awal"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="dateAkhir"
                    label="Sampai Dengan"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
              </>
            )}
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">Print</button>
              </div>
            </div>
          </div>
        );

      case "transaksi":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <div className="form-group">
                  <Field
                    name="kode_supplier"
                    component={ReanderSelect}
                    options={this.props.listsupplier.map((list) => {
                      let data = {
                        value: list.kode_supplier,
                        name: list.nama_supplier,
                      };
                      return data;
                    })}
                    type="text"
                    label="Kode Supplier"
                    placeholder="Masukan Kode Supplier"
                  />
                </div>
              </div>

              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sampai Dengan"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.onSend}
                  >
                    {this.props.onSend ? (
                      <span
                        className="spinner-border spinner-border-sm mr-3"
                        aria-hidden="true"
                      ></span>
                    ) : (
                      <i className="fa fa-print mr-2"></i>
                    )}
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "tolakan":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_supplier"
                  component={ReanderSelect}
                  options={this.props.listsupplier.map((list) => {
                    let data = {
                      value: list.kode_supplier,
                      name: list.nama_supplier,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Supplier"
                  placeholder="Masukan Kode Supplier"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_awal"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_akhir"
                  label="Sampai Dengan"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.onSend}
                  >
                    {this.props.onSend ? (
                      <span
                        className="spinner-border spinner-border-sm mr-3"
                        aria-hidden="true"
                      ></span>
                    ) : (
                      <i className="fa fa-print mr-2"></i>
                    )}
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "titip":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_supplier"
                  component={ReanderSelect}
                  options={this.props.listsupplier.map((list) => {
                    let data = {
                      value: list.kode_supplier,
                      name: list.nama_supplier,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Supplier"
                  placeholder="Masukan Kode Supplier"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="type"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "SIMPLE",
                      name: "SIMPLE",
                    },
                    {
                      value: "CARD",
                      name: "CARD",
                    },
                    {
                      value: "SUMMARY",
                      name: "SUMMARY",
                    },
                  ]}
                  type="text"
                  label="Type Laporan"
                  placeholder="Masukan Type Laporan"
                  onChange={(data) =>
                    this.setState({
                      type_laporan: data,
                    })
                  }
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="jenis_transkasi"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "GRAM",
                      name: "GRAM",
                    },
                    {
                      value: "RUPIAH",
                      name: "RUPIAH",
                    },
                  ]}
                  type="text"
                  label="Filter By"
                  placeholder="Masukan Filter By"
                />
              </div>
              <div
                className={
                  this.state.type_laporan === "SIMPLE"
                    ? "col-lg-3 d-none"
                    : "col-lg-3"
                }
              >
                <Field
                  name="tanggal_awal"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div
                className={
                  this.state.type_laporan === "SIMPLE"
                    ? "col-lg-3 d-none"
                    : "col-lg-3"
                }
              >
                <Field
                  name="tanggal_akhir"
                  label="Sampai Dengan"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.onSend}
                  >
                    {this.props.onSend ? (
                      <span
                        className="spinner-border spinner-border-sm mr-3"
                        aria-hidden="true"
                      ></span>
                    ) : (
                      <i className="fa fa-print mr-2"></i>
                    )}
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      default:
        break;
    }
  }
}

formSupplier = reduxForm({
  form: "formSupplier",
  enableReinitialize: true,
})(formSupplier);
export default connect((state) => {
  return {
    listsupplier: state.provinsi.supplier,
    onSend: state.provinsi.onSend,
    initialValues: {
      tanggal_awal: getToday(),
      tanggal_akhir: getToday(),
      tglFrom: getToday(),
      tglTo: getToday()
    }
  };
})(formSupplier);
