import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram, formatRp, getUserID, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPrintReturnSupplier = (
  row1isi = "",
  row2isi = "",
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 40;
  doc.setFontSize(15);
  doc.text("LAPORAN RETURN SUPPLIER - DETAIL", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);
  doc.setFontSize(10);
  //row 1
  doc.text(`Mulai Dari : ${row1isi}`, 14, 39);
  //   row 2
  doc.text(`Sampai Dengan : ${row2isi}`, 100, 39);
  data.forEach((item, index) => {
    doc.text(`Kode Supplier : ${item.kode_supplier}`, 14, 44);
    doc.text(`Nama Supplier : ${item.nama_supplier}`, 100, 44);
    item.detail.forEach((list, index) => {
      tableColumn = [
        [
          {
            content: `TANGGAL INVOICE : ${list.tanggal_invoice}`,
            colSpan: 12,
          },
        ],
        [
          {
            content: `NO BON : ${list.no_bon}`,
            colSpan: 12,
          },
        ],
        [
          {
            content: `NO`,
          },
          {
            content: `KODE JENIS`,
          },
          {
            content: `KETERANGAN`,
          },
          {
            content: `CAP KODE`,
            styles: { halign: "right", cellWidth: 16 },
          },
          {
            content: `KODE AWAL`,
            styles: { halign: "right", cellWidth: 16 },
          },
          {
            content: `QTY`,
            styles: { halign: "right" },
          },
          {
            content: `P`,
            styles: { halign: "right" },
          },
          {
            content: `K`,
            styles: { halign: "right" },
          },
          {
            content: `PKG`,
            styles: { halign: "right" },
          },
          {
            content: `GROSS`,
            styles: { halign: "right" },
          },
          {
            content: `BRUTO`,
            styles: { halign: "right" },
          },
          {
            content: `NETTO`,
            styles: { halign: "right" },
          },
          {
            content: `ONGKOS`,
            styles: { halign: "right" },
          },
        ],
      ];
      list.detail_barang.forEach((barang, index) => {
        let rows = [
          barang.no_urut,
          barang.kode_jenis,
          barang.keterangan,
          { content: barang.kadar_cetak, styles: { halign: "right" } },
          { content: barang.kadar_beli, styles: { halign: "right" } },
          { content: barang.qty, styles: { halign: "right" } },
          { content: formatGram(barang.plastik), styles: { halign: "right" } },
          { content: formatGram(barang.kulit), styles: { halign: "right" } },
          { content: formatGram(barang.pkg), styles: { halign: "right" } },
          { content: formatGram(barang.gross), styles: { halign: "right" } },
          { content: formatGram(barang.bruto), styles: { halign: "right" } },
          { content: formatGram(barang.netto, 3), styles: { halign: "right" } },
          { content: formatRp(barang.ongkos_rp), styles: { halign: "right" } },
        ];
        tableRows.push(rows);
        console.log(tableRows);
      });
      let footer = [
        { content: "Grand Total", styles: { halign: "right" }, colSpan: 10 },
        { content: formatGram(list.total_bruto), styles: { halign: "right" } },
        {
          content: formatGram(list.total_netto, 3),
          styles: { halign: "right" },
        },
        { content: formatRp(list.total_rp), styles: { halign: "right" } },
      ];
      footRows.push(footer);
      doc.autoTable({
        head: tableColumn,
        body: tableRows,
        foot: footRows,
        startY: index === 0 ? 50 : finalY + 5,
        theme: "plain",
        rowPageBreak: "avoid",
        pageBreak: "avoid",
        margin: { top: 20 },
        bodyStyles: { lineColor: "#969696",fontSize : 8, lineWidth: 0.05},
      headStyles: {
        lineColor: "#969696",
        lineWidth: 0.05,
        fontSize : 8,
        fillColor: "#E8E8E8",
      },
      footStyles: {
        lineColor: "#969696",
        lineWidth: 0.05,
        fillColor: "#E8E8E8",fontSize : 8,
      },
      });
      finalY = doc.autoTableEndPosY() + 10;
      tableRows = [];
      footRows = [];
    });
  });
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  doc.text(`Cetak By	: ${getUserID()}`, 14, finalY + 10);

  doc.text(`Cetak	Date : ${tanggal}`, 14, finalY + 16);

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN RETURN SUPPLIER - DETAIL",
  });
  // doc.save(`RETURNSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN RETURN SUPPLIER - DETAIL</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPrintReturnSupplier;
