import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatOptional, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanTitipSupplierCard = (data, mulai_dari, sampai_dengan) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let tableColumn = [];
  doc.setFontSize(15);
  doc.text("LAPORAN TITIP SUPPLIER ", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);

  doc.setFontSize(10);
  doc.text(`Mulai Dari : ${mulai_dari}`, 14, 39);
  //   row 2
  doc.text(`Sampai Dengan	: ${sampai_dengan}`, 100, 39);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    tableColumn = [
      [
        {
          content: `TANGGAL`,
        },
        {
          content: `AWAL\nTITIP`,
          styles: { halign: "right" },
        },
        {
          content: `IN\nTITIP`,
          styles: { halign: "right" },
        },
        {
          content: `OUT\nTITIP`,
          styles: { halign: "right" },
        },
        {
          content: `AKHIR\nTITIP`,
          styles: { halign: "right" },
        },
        {
          content: `NO BON`,
        },
      ],
    ];
    let rows = [
      item.tanggal,
      { content: formatOptional(item.awal_titip), styles: { halign: "right" } },
      { content: formatOptional(item.in_titip), styles: { halign: "right" } },
      { content: formatOptional(item.out_titip), styles: { halign: "right" } },
      {
        content: formatOptional(item.akhir_titip),
        styles: { halign: "right" },
      },
      item.no_bon,
    ];
    tableRows.push(rows);
  });
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: 45,
    theme: "plain",
    rowPageBreak: "avoid",

    margin: { top: 20 },
    bodyStyles: { lineColor: "#969696",fontSize : 8, lineWidth: 0.05},
      headStyles: {
        lineColor: "#969696",
        lineWidth: 0.05,
        fontSize : 8,
        fillColor: "#E8E8E8",
      },
      footStyles: {
        lineColor: "#969696",
        lineWidth: 0.05,
        fillColor: "#E8E8E8",fontSize : 8,
      },
  });
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN TITIP SUPPLIER - CARD",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN TITIP SUPPLIER - CARD</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanTitipSupplierCard;
