import React, { Component } from "react";
import { connect } from "react-redux";
import { finishSend, progressSend } from "../actions/LogicAction";
import { gagal } from "../Alert";
import { AxiosMasterGet } from "../Axios";
import { getToday } from "../GlobalComponent/function";
import { checkLaporan, getUserID } from "../helper";
import FormSupplier from "./Component/formSupplier";
import LaporanHutangSupplier from "./Component/LaporanHutangSupplier";
import LaporanHutangSupplierCard from "./Component/LaporanHutangSupplierCard";
import LaporanHutangSupplierSummary from "./Component/LaporanHutangSupplierSummary";
import LaporanPembayaranSupplier from "./Component/LaporanPembayaranSupplier";
import LaporanPrintPenerimaanSupplier from "./Component/LaporanPrintPenerimaanSupplier";
import LaporanPrintPenerimaanSupplierRekap from "./Component/LaporanPrintPenerimaanSupplierRekap";
import LaporanPrintReturnSupplier from "./Component/LaporanPrintReturnSupplier";
import LaporanPrintReturnSupplierRekap from "./Component/LaporanPrintReturnSupplierRekap";
import LaporanTitipSupplier from "./Component/LaporanTitipSupplier";
import LaporanTitipSupplierCard from "./Component/LaporanTitipSupplierCard";
import LaporanTitipSupplierSummary from "./Component/LaporanTitipSupplierSummary";
import LaporanTolakanSupplier from "./Component/LaporanTolakanSupplier";

class LaporanSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLaporan: [],
    };
  }

  handlePenerimaan(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      "report/terima-barang-supplier/" +
        `${data.type}&${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          data.type === "DETAIL"
            ? LaporanPrintPenerimaanSupplier(
                data.tanggal_awal,
                data.tanggal_akhir,
                getUserID(),
                getToday(),
                getUserID(),
                res.data
              )
            : LaporanPrintPenerimaanSupplierRekap(
                data.tanggal_awal,
                data.tanggal_akhir,
                getUserID(),
                getToday(),
                getUserID(),
                res.data
              );
        }
      })
      .catch((err) =>
        gagal(
          `Gagal Mengambil data, Mohon periksa koneksi anda, Detail :${err.response.data}`
        ).then(() => this.props.dispatch(finishSend()))
      );
  }
  handleHutang(data) {
    switch (data.type) {
      case "SIMPLE":
        this.props.dispatch(progressSend());
        AxiosMasterGet("report/hutang-supplier/" + data.kode_supplier)
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanHutangSupplier(res.data);
            }
          })
          .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
          .then(() => this.props.dispatch(finishSend()));
        break;
      case "CARD":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          "report/hutang-supplier-card/" +
            `${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanHutangSupplierCard(
                res.data,
                data.tanggal_awal,
                data.tanggal_akhir
              );
            }
          })
          .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
          .then(() => this.props.dispatch(finishSend()));
        break;
      case "SUMMARY":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          "report/hutang-supplier-summary/" +
            `${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanHutangSupplierSummary(
                res.data,
                data.tanggal_awal,
                data.tanggal_akhir
              );
            }
          })
          .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
          .then(() => this.props.dispatch(finishSend()));
        break;
      default:
        break;
    }
  }
  handleKartuHutang(data) {
    console.log(data);
  }
  handleOmzet(data) {}
  handleReturn(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      "report/retur-barang-supplier/" +
        `${data.type}&${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          data.type === "DETAIL"
            ? LaporanPrintReturnSupplier(
                data.tanggal_awal,
                data.tanggal_akhir,
                getUserID(),
                getToday(),
                getUserID(),
                res.data
              )
            : LaporanPrintReturnSupplierRekap(
                data.tanggal_awal,
                data.tanggal_akhir,
                getUserID(),
                getToday(),
                getUserID(),
                res.data
              );
        }
      })
      .catch((err) =>
        gagal(
          `Gagal Mengambil data, Mohon periksa koneksi anda, Detail :${err.response.data}`
        ).then(() => this.props.dispatch(finishSend()))
      );
  }
  handleTitip(data) {
    switch (data.type) {
      case "SIMPLE":
        this.props.dispatch(progressSend());
        AxiosMasterGet(`report/titip-supplier/${data.kode_supplier}`)
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanTitipSupplier(res.data)
            }
          })
          .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
          .then(() => this.props.dispatch(finishSend()));
        break;
      case "CARD":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/titip-supplier-card/${data.kode_supplier}&${data.jenis_transkasi}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanTitipSupplierCard(
                res.data,
                data.tanggal_awal,
                data.tanggal_akhir
              );
            }
          })
          .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
          .then(() => this.props.dispatch(finishSend()));
        break;
      case "SUMMARY":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/titip-supplier-summary/${data.kode_supplier}&${data.jenis_transkasi}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              this.setState({
                listLaporan: res && res.data,
              });
            }
          })
          .then(() =>
            LaporanTitipSupplierSummary(
              this.state.listLaporan,
              data.tanggal_awal,
              data.tanggal_akhir
            )
          )
          .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
          .then(() => this.props.dispatch(finishSend()));
        break;
      default:
        break;
    }
  }
  handleTolakan(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `report/tolakan-supplier/${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanTolakanSupplier(
            data.kode_supplier,
            data.tanggal_awal,
            data.tanggal_akhir,
            getToday(),
            "ADMIN",
            res.data
          );
        }
      })
      .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
      .then(() => this.props.dispatch(finishSend()));
  }
  handleTransaksi(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `report/bayar-hutang-supplier/${data.kode_supplier}&${data.tglFrom}&${data.tglTo}&REKAP&REKAP`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanPembayaranSupplier(res.data, data.tglFrom, data.tglTo);
        }
      })
      .catch((err) => gagal(`Get Data Error , Detail : ${err}`))
      .then(() => this.props.dispatch(finishSend()));
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                  <li className="nav-item">
                    <a
                      href="#penerimaan-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link active"
                    >
                      <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Penerimaan</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#return-tab"
                      data-toggle="tab"
                      aria-expanded="true"
                      className="nav-link "
                    >
                      <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Return</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#hutang-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Hutang</span>
                    </a>
                  </li>
                  {/* <li className="nav-item">
                    <a
                      href="#kartu-hutang-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Kartu Hutang</span>
                    </a>
                  </li> */}
                  {/* <li className="nav-item">
                    <a
                      href="#omzet-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Omzet</span>
                    </a>
                  </li> */}
                  <li className="nav-item">
                    <a
                      href="#transaksi-pembayaran-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Transaksi Pembayaran
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#tolakan-return-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Tolakan Supplier
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#titip-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Titip</span>
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div className="tab-pane show active" id="penerimaan-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handlePenerimaan(data)}
                    />
                  </div>
                  <div className="tab-pane" id="return-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handleReturn(data)}
                    />
                  </div>
                  <div className="tab-pane" id="hutang-tab">
                    <FormSupplier
                      from="hutang"
                      onSubmit={(data) => this.handleHutang(data)}
                    />
                  </div>
                  <div className="tab-pane" id="kartu-hutang-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handleKartuHutang(data)}
                    />
                  </div>
                  <div className="tab-pane" id="omzet-tab">
                    <FormSupplier
                      from="omzet"
                      onSubmit={(data) => this.handleOmzet(data)}
                    />
                  </div>
                  <div className="tab-pane" id="transaksi-pembayaran-tab">
                    <FormSupplier
                      from="transaksi"
                      onSubmit={(data) => this.handleTransaksi(data)}
                    />
                  </div>
                  <div className="tab-pane" id="tolakan-return-tab">
                    <FormSupplier
                      from="tolakan"
                      onSubmit={(data) => this.handleTolakan(data)}
                    />
                  </div>
                  <div className="tab-pane" id="titip-tab">
                    <FormSupplier
                      from="titip"
                      onSubmit={(data) => this.handleTitip(data)}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(LaporanSupplier);
