import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatOptional, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPembayaranSupplier = (data, mulai_dari, sampai_dengan) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  doc.setFontSize(15);
  doc.text("LAPORAN PEMBAYARAN HUTANG SUPPLIER", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);
  doc.setFontSize(10);
  doc.text(`Mulai Dari : ${mulai_dari}`, 14, 39);
  //   row 2
  doc.text(`Sampai Dengan	: ${sampai_dengan}`, 100, 39);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    let tableColumn = [
      [
        {
          content: `KODE SUPPLIER : ${item.kode_supplier} / ${item.nama_supplier}`,
          colSpan: 8,
        },
      ],
      [
        {
          content: `TANGGAL`,
        },
        {
          content: `NO BON`,
        },
        {
          content: `JUMLAH HUTANG`,
          styles: { halign: "right" },
        },
        {
          content: `PUTUS TITIP`,
          styles: { halign: "right" },
        },
        {
          content: `DENDA`,
          styles: { halign: "right" },
        },
        {
          content: `HUTANG`,
          styles: { halign: "right" },
        },
        {
          content: `PEMBAYARAN`,
          styles: { halign: "right" },
        },
        {
          content: `LBH BAYAR`,
          styles: { halign: "right" },
        },
      ],
    ];
    item.detail.forEach((hasil) => {
      let rows = [
        hasil.tanggal,
        hasil.no_bon,
        {
          content: formatOptional(hasil.jumlah_hutang),
          styles: { halign: "right" },
        },
        {
          content: formatOptional(hasil.total_putus_titip),
          styles: { halign: "right" },
        },
        {
          content: formatOptional(hasil.total_denda),
          styles: { halign: "right" },
        },
        {
          content: formatOptional(hasil.total_hutang),
          styles: { halign: "right" },
        },
        {
          content: formatOptional(hasil.total_pembayaran),
          styles: { halign: "right" },
        },
        {
          content: formatOptional(hasil.lebih_pembayaran),
          styles: { halign: "right" },
        },
      ];
      tableRows.push(rows);
      let detail = [
        `BAYAR CASH : ${item.detail_bayar[index].bayar_cash}`,
        `BAYAR TRANSFER : ${item.detail_bayar[index].bayar_trf}`,
        `BAYAR CIOK : ${item.detail_bayar[index].bayar_ciok}`,
        `BAYAR RONGSOK : ${item.detail_bayar[index].bayar_rongsok}`,
        `BAYAR GIRO : ${item.detail_bayar[index].bayar_giro}`,
      ];
      tableRows.push(detail);
      tableRows.push([
        {
          content: "",
          colSpan: 8,
          styles: {
            fillColor: [212, 212, 211],
          },
        },
      ]);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      startY: 45,
      theme: "plain",
      rowPageBreak: "avoid",

      margin: { top: 20 },
      bodyStyles: { lineColor: "#969696",fontSize : 8, lineWidth: 0.05},
        headStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fontSize : 8,
          fillColor: "#E8E8E8",
        },
        footStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fillColor: "#E8E8E8",fontSize : 8,
        },
    });
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN PEMBAYARAN HUTANG SUPPLIER",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PEMBAYARAN HUTANG SUPPLIER</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPembayaranSupplier;
